import React, {Component} from 'react'
import bgPost from '../images/bg-post.png'


export default class Card extends Component {
	render() {
		const thumbnail = this.props.thumbnail
		const title = this.props.title
		const title2 = this.props.title2
		const content = this.props.content
		const author = this.props.author
		const time = this.props.time

		return (
			<div className="article__post">
			<div className="article__thumbnail-holder article-thumbnail">
				<img src={thumbnail}alt="Img"/>
			</div>
			<div className="article__title-holder">
				<p className="article-title"> {title}
				<span> {title2}</span></p>
			</div>
			<div className="article__content-holder">
				<p className="article-content">
					{content}
				</p>
			</div>
			<div className="article__author-holder">
				<p className="article-author">{author}</p>
				<p className="article-time">{time}</p>
			</div>
		</div>
		)
			
		}
}